html {
  font-size: 10px;
}

body {
  margin: 0;
  background-color: #000;
  color: #fff;
}

.application {
  height: calc(100vh - 20px);
  width: calc(100vw - 35px);
  max-width: 1440px;
  margin: 0 auto;
  background-image: url("../img/bg-main.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center bottom;
}

.header {
  display: flex;
  align-items: center;
  height: 10%;
  margin-top: 20px;
}

.content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 80%;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 10%;
}

.footer-logo {
  width: 85px;
  height: 31px;
  background-image: url("../img/svg/rs_school.svg");
}

.info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 10%;
}
.info a {
  text-decoration: none;
  color: #fff;
}

.button-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 41%;
}

.button {
  width: 287px;
  height: 62px;
  background-color: transparent;
  color: #fff;
  border: 1.5px solid white;
  border-radius: 60px;
  transition: box-shadow 1s ease;
}
.button:hover {
  transform: scale(1.02);
  box-shadow: 5px 5px 15px rgba(255, 255, 255, 0.7);
  cursor: pointer;
}

.settings {
  width: 36px;
  height: 36px;
  margin-left: auto;
  background-image: url("../img/svg/settings.svg");
  transition: transform 0.8s ease-in-out;
}
.settings:hover {
  transform: rotate(180deg);
}

.logo {
  width: 100%;
  height: 165px;
  margin-top: 140px;
  background-image: url("../img/logo-main.png");
  background-repeat: no-repeat;
  background-position: center;
}